
import { defineComponent } from 'vue';
import IUser from '../interfaces/IUser';

export default defineComponent({
    data() {
        return {
            // Current user item
            activeUser: null as number | null,

            // New user inputs
            newName: '',
            newUsername: '',
            newEmail: '',
            newPhone: '',

            // Filters
            usersNumber: 'all',

            // Sort
            sortedBy: '',
        }
    },
    computed: {
        // Users from API
        users(): IUser[] {
            return this.$store.getters.getUsers
        },

        // Filtered users
        filteredUsers(): IUser[] {
            if(this.usersNumber !== 'all') {
                return this.users.slice(0, +this.usersNumber)
            } else {
                return this.users
            }
        },

        // Search input from header component
        searchInput(): string {
            return this.$store.getters.getSearchInput
        },
        // Searched users
        searchedUsers(): IUser[] {
            return this.filteredUsers.filter(user => user.name.toLowerCase().includes(this.searchInput.toLowerCase()))
        },

        // Sorted users
        sortedUsers(): IUser[] {
            if(this.sortedBy) {
                return this.searchedUsers.slice().sort((a: any, b: any) => a[this.sortedBy].localeCompare(b[this.sortedBy]))
            } else {
                return this.searchedUsers
            }
        },
    },
    methods: {
        editUser(i: number) {
            this.newName = ''
            this.newUsername = ''
            this.newEmail = ''
            this.newPhone = ''
            this.activeUser = i;
        },
        saveChanges(i: number) {
            if(this.newName) {
                this.users[i].name = this.newName
                this.newName = ''
            }
            if(this.newUsername) {
                this.users[i].username = this.newUsername
                this.newUsername = ''
            }
            if(this.newEmail) {
                this.users[i].email = this.newEmail
                this.newEmail = ''
            }
            if(this.newPhone) {
                this.users[i].phone = this.newPhone
                this.newPhone = ''
            }
            this.activeUser = null
        },
        cancelChanges() {
            this.newName = ''
            this.newUsername = ''
            this.newEmail = ''
            this.newPhone = ''
            this.activeUser = null
        },
        deleteUser(i: number) {
            this.newName = ''
            this.newUsername = ''
            this.newEmail = ''
            this.newPhone = ''
            this.$store.dispatch('deleteUser', i)
        },
    },
    mounted() {
        // Fetch data from API
        this.$store.dispatch('fetchUsers');
    }
});
